<template>
  <div>
    <nav class="navbar navbar-transparent navbar-absolute">
      <div class="container">
        <div class="navbar-header">
          <button type="button" class="navbar-toggle"
                  data-toggle="collapse"
                  data-target="#navigation-example-2"
                  @click="toggleNavbar">
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <!-- <router-link class="navbar-brand" to="/admin">Paper Dashboard PRO</router-link> -->
        </div>

      </div>
    </nav>

    <div class="wrapper wrapper-full-page">
      <div class="full-page login-page" data-color="blue"
           data-image="static/img/background/background-2.jpg">
        <!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
        <div class="content">
          <div class="container">


           <div class="row">
              <div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3">
                
                  <div class="card" data-background="color" data-color="blue">
                    <div class="card-header">
                      <h3 class="card-title" style="text-align:center">Login</h3>
                    </div>
                    <div class="card-content">
                  <div class="form-group">
                      <input type="text"
                             name="username"
                             v-validate="modelValidations.username"
                             v-model="model.username"
                             placeholder="Username" 
                             class="form-control">
                      <small class="text-danger" v-show="username.invalid">
                        {{ getError('username') }}
                      </small>
              
                   </div>
                   <div class="form-group">
                    <input type="password"
                           name="password"
                           v-validate="modelValidations.password"
                           v-model="model.password"
                           placeholder="Password" 
                           class="form-control">
                    <small class="text-danger" v-show="password.invalid">
                      {{ getError('password') }}
                    </small>
                  </div>
                    </div>
                    <div class="card-footer text-center">
                      <button @click="login(),validate()" type="" class="btn btn-fill btn-wd " :disabled="errors.any()">Submit</button>
                      <div class="forgot">
                      </div>
                    </div>
                  </div>
              </div>
            </div>
          </div>
        </div>
        <footer class="footer footer-transparent">
          <div class="container">

          </div>
        </footer>
        <div class="full-page-background" style="background-image: url(static/img/background/background-2.jpg) "></div>
      </div>
    </div>
    <div class="collapse navbar-collapse off-canvas-sidebar">
      <ul class="nav nav-mobile-menu">
        <router-link to="/register" tag="li">
          <a>Register</a>
        </router-link>
        <router-link to="/admin/overview" tag="li">
          <a>Dashboard</a>
        </router-link>
      </ul>
    </div>
        <div class="collapse navbar-collapse off-canvas-sidebar">
      <ul class="nav nav-mobile-menu">
        <router-link to="/register" tag="li">
          <a>Register</a>
        </router-link>
        <router-link to="/admin/overview" tag="li">
          <a>Dashboard</a>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>

  import Router from 'vue-router'
  import {mapFields} from 'vee-validate'
  import axios from 'axios'
  import swal from 'sweetalert2'


  export default {
    computed: {
      ...mapFields(['username', 'password'])
    },
    data () {
      return {
        model: {
          username: '',
          idDestination: ''
        },
        modelValidations: {
          username: {
            required: true
          },
          password: {
            required: true,
            min: 5
          }
        }
      }
    },
    methods: {
      toggleNavbar () {
        document.body.classList.toggle('nav-open')
      },
      closeMenu () {
        document.body.classList.remove('nav-open')
        document.body.classList.remove('off-canvas-sidebar')
      },
      navigate(){
        console.log('yeah');
       this.$router.push('/')
      },
      //vee validate
      getError (fieldName) {
      return this.errors.first(fieldName)
      },
      validate () {
        this.$validator.validateAll().then(isValid => {
          this.$emit('on-submit', this.registerForm, isValid)
        })
      },
      submit(){
        console.log("username: ", this.model.username);
        console.log("password: ", this.model.password);
      },
         login() {
            if(this.model.username != "" && this.model.password != "" || this.model.username != "" && this.model.password != 'undefined') {
                  const data = {
                  "query":"query($username:String!,$password:String!){auth(username:$username,password:$password)}",
                  "variables":{
                  "username":this.model.username,
                  "password":this.model.password
                  }
                }
                const config ={
                  "headers":{
                    "Content-Type":"application/json"
                  }
                }
                  var thisToken = ""
                    axios.post(this.post_url,JSON.stringify(data),this.post_config).then((response) => {
                    thisToken = response.data.data.auth;
                        if(!thisToken){
                        this.showSwal('wrong')
                        }else{
                          window.localStorage.setItem("session",thisToken);
                          if(this.validate_session() == true){
                            this.$router.push('/')
                            this.showSwal('success')
                          }else{
                            this.showSwal('wrong')
                          }
                        }
                   //window.localStorage.setItem('session',response.data.data.auth);
                   }).catch(err =>{
                     console.log(err.response)
                   });                                
         }
      },
       showSwal (type) {
        if (type === 'wrong') {
          swal({
            title: `Login Failed`,
            text: `Invalid username or password`,

            buttonsStyling: false,
            type: 'warning',
            confirmButtonClass: 'btn btn-success btn-fill'
          })
        }else if(type === 'success') {
          swal({
            title: `Login Success`,
            text: `Welcome to our Admin Panel`,
            buttonsStyling: false,
            type: 'success',
            confirmButtonClass: 'btn btn-success btn-fill'
          })
         }
        
      }

    },//method
    beforeDestroy () {
      this.closeMenu()
    },
    mounted(){
        if(this.validate_session() == true){
            this.$router.push('/')
          }else if(this.validate_session() == -1){
          }else{
          }
    }  

  };
</script>
<style>
</style>



