// 
import jwtDecode from 'jwt-decode'; 

export default {

	data() {
    	return {
    		// "http://172.16.39.21:7861/query/"
    		// "https://api.linejr.com/query/"
    		post_url:"https://api.linejr.com/query/",
    		post_config:{
    			"headers":{
                    "Content-Type":"application/json"
                }
    		},
    		post_config_auth:{
    			"headers":{
                    "Content-Type":"application/json",
                    "Authorization":window.localStorage.getItem("session")
                }
    		}
        }
   	},
	methods:{
		validate_session(){
		  // console.log("called this validate_session!");
	      const dateNow = new Date();
	      const sessiondata =  window.localStorage.getItem("session");
	      if(!sessiondata){
		  	this.$router.push('/login')
	      	return null
	      }else{
	        const decoded = jwtDecode(sessiondata);
	        if((dateNow.getTime()/1000) <= decoded.exp){
	        	if(decoded.roles.indexOf('administrator') == -1){
	        		return -1;
	        	}else{
	        		return true;
	        	}
	        }else{
	        	return false;
	        }
	      }
	    },
	    logout(){
	    	// console.log("called logout!");
	    	window.localStorage.removeItem("session");
	    	this.validate_session();
	    },
	  

	    

	},
}


