<template>
  <div class="row">
      <div class="col-md-12">
        <div v-for="(items,index) in newsArray">
           {{editStyle(items.id)}}

           <div class="card" :class="{editHide:style[items.id],editShow:!style[items.id]}">
              <div class="card-header">
                <h4 class="card-title">{{items.headline}}</h4>
                <div class="btn-magnify"><i class="edit-class ti-pencil-alt" @click="addthis(items)"></i></div>
                <p class="category">&nbsp;<i class="ti-calendar"></i> &nbsp;{{convertdate(items.publishDate)}}</p> 
              </div>
              <hr>
              <div class="card-content">
                <div class="typo-line">
                  <blockquote>
                    <el-collapse>
                        <el-collapse-item title="Header Image" name="1">
                        <div>
                            <img :src="items.headerImage.text" style="width:100%">
                        </div>
                      </el-collapse-item>
                      <el-collapse-item title="Content" name="2">
                        <div>
                            <p v-html="items.content"></p>
                        </div>
                      </el-collapse-item>
                    
                    </el-collapse>
                    <br />
                    <small> {{items.author}}</small>
                  </blockquote>
                </div>
              </div>
              <hr>
              <div class="card-footer">
                 <button class="btn">Publish</button>
              </div>
            </div>
            <!-- edit -->
            <div class="card" :class="{editHide:!style[items.id],editShow:style[items.id]}">
                <div class="card-header">
                </div>
                <div class="card-content">
                <div class="form-horizontal">
                  <fieldset>
                    <div class="form-group">
                      <label class="col-sm-1 control-label">Title</label>
                      <div class="col-sm-10">
                          <input type="text" 
                                  name="Title"
                                 class="form-control"
                                 v-model="items.headline"
                                 v-validate="modelValidations.Title">
                        <small class="text-danger" v-show="Title.invalid">
                        {{ getError('Title') }}
                      </small>
                      </div>
                    </div>
                  </fieldset>
                  <fieldset>
                    <div class="form-group">
                      <label class="col-sm-1 control-label">Image</label>
                      <div class="col-sm-10">
                          <input type="text" 
                                  name="Image"
                                 class="form-control"
                                  v-model="items.headerImage.text"
                                 v-validate="modelValidations.Image">
                        <small class="text-danger" v-show="Image.invalid">
                        {{ getError('Image') }}
                      </small>
                      </div>
                    </div>
                  </fieldset>
                  <fieldset>
                    <div class="form-group">
                      <label class="col-sm-1 control-label"></label>
                      <div class="col-sm-10">
                       <vue-editor v-model="items.content">
                          
                        </vue-editor>
                      </div>
                    </div>
                  </fieldset>
                  <fieldset>
                    <div class="form-group">
                      <label class="col-sm-1 control-label">Author</label>
                       <div class="col-sm-10">
                          <input type="text" 
                                  name="Author"
                                 class="form-control"
                                 v-model="items.author"
                                 v-validate="modelValidations.Author">
                        <small class="text-danger" v-show="Author.invalid">
                        {{ getError('Author') }}
                      </small>
                      </div>
                    </div>
                    <div class="form-group">
                      <label class="col-sm-1 control-label">Date Picker</label>
                      <div class="col-sm-10">
                        <div class="col-md-4">
                        <div class="form-group">
                          <el-date-picker  v-model="items.publishDate" type="date" placeholder="Pick a day"
                            :picker-options="pickerOptions1">
                          </el-date-picker>
                        </div>
                      </div>
                      </div>
                    </div>
                  </fieldset>
                  <fieldset>
                    <div class="form-group">
                      <label class="col-sm-1 control-label">Tags</label>
                       <div class="col-sm-10">
                          <input type="text" 
                                  name="Tags"
                                 class="form-control"
                                 v-model="items.tags"
                                 v-validate="modelValidations.Tags">
                        <small class="text-danger" v-show="Tags.invalid">
                        {{ getError('Tags') }}
                      </small>
                      </div>
                    </div>
                  </fieldset>
                  </div>
                  <div class="card-footer">.
                    <div  style="float:right">
                   <button class="btn btn-xs btn-fill btn-info" :disabled="errors.any()" @click="showSwal('warning-message-and-cancel'),saveTempMethod(items)">Save</button>
                   <button class="btn btn-xs btn-fill   btn-danger" @click="deleteThis(items.id)">Cancel</button>
                    </div>
                </div>
                </div>
            </div>
            <!-- news array loop -->
            </div>
        </div>
      </div>
    </div>
</template>
<script>

import { VueEditor } from 'vue2-editor'
import Datepicker from 'vuejs-datepicker';
import axios from 'axios';
import Task from './Task.vue'
import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
import {mapFields} from 'vee-validate'
import {Collapse, CollapseItem} from 'element-ui'
import VueTabs from 'vue-nav-tabs'
import Vue from 'vue'
import swal from 'sweetalert2'

Vue.use(VueTabs)
Vue.use(Collapse)
Vue.use(CollapseItem)

export default {
name: 'newsfeed',
computed: {
  ...mapFields(['Title', 'Image','Text','Date','Tags','Author'])
},
components: {
  'date-picker': Datepicker,
  'vue-editor' :VueEditor,
  Task,
  [DatePicker.name]: DatePicker
 },
data () {
return {    
    modelValidations: {
      Title: {
        required: true
      },
      Image: {
        required: true,
      },
      Text: {
        required: true,
      },
      Date: {
        required: true,
      },
      Tags: {
        required: true,
      },
      Author: {
        required: true,
      },
    },    
  pickerOptions1: {
    shortcuts: [{
      text: 'Today',
      onClick (picker) {
        picker.$emit('pick', new Date())
      }
    },
    {
      text: 'Yesterday',
      onClick (picker) {
        const date = new Date()
        date.setTime(date.getTime() - 3600 * 1000 * 24)
        picker.$emit('pick', date)
      }
    },
    {
      text: 'A week ago',
      onClick (picker) {
        const date = new Date()
        date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
        picker.$emit('pick', date)
      }
    }]
  },
  datePicker: '',
  style:{},
  newsArray: [],
  content:' ',
  itemsHeadline:'',
  editParams: ' ',
  saveTemp:{},
  }
},
methods: {
convertdate(date_var){
    var d = new Date(date_var),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear(),
        today = '' + d.getDay().toString();

    var thisDay = "";
        if(today == "1" ){
          thisDay = "Nonday"
        }else if(today == "2"){
          thisDay = "Tuesday"

        }else if(today == "3"){
          thisDay = "Wednesday"

        }else if(today == "4"){
          thisDay = "Thursday"

        }else if(today == "5"){
          thisDay = "Friday"
        }else if(today == "6"){
          thisDay = "Saturday"
        }
         else {
          thisDay = "Sunday"
        }
    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return year +"-"+ day +"-"+ month + " " +  thisDay;
},
removeThisPreview(){
  console.log("break na tayo beh!");
},
previewThis(obj) {
    this.preview.id=obj.id
    this.preview.author=obj.author
    this.preview.content=obj.content
    this.preview.headerImage=obj.headerImage.text
    this.preview.headline=obj.headline
    this.preview.publishDate=obj.publishDate
    this.preview.tags = obj.tags;
},
editStyle(stat){
  if (this.style[stat] === undefined) {
    this.$set(this.style,stat,false) 
  }
},
publish_news(){
  const data = {
  "query":"query{publishNews(newsID:\""+this.publishTemp+"\")}"
  }
  axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
  if(response.data.data.publishNews == "success"){
  this.getNews()
  }
  console.log(response);
  }).catch((err) => {
  console.log(err);
  });
},
addthis(obj) {
  this.$set(this.style,obj.id,true) 
},
deleteThis(id){
    this.$set(this.style,id,false) 
},
getNews(){
const data = {
"query":
`query{getNews{ id,headline,tags, headerImage{text,isFile},author,content, publishDate,status }}`
}
var thisGetNews = "";
axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
this.newsArray = response.data.data.getNews
}).catch((err) => {
console.log(err);
});
},
async saveThis(){
var tagsArray = [];
       

      let data = {
           "query":"mutation($hasid:String,$publishDate:String,$headline:String!,$image:String,$author:String,$content:String!,$tags:[String]){news(headline:$headline,image:$image,author:$author,content:$content,tags:$tags,hasID:$hasid,publishDate:$publishDate)}",
          "variables":{
          "hasid":this.saveTemp.id,
          "headline":this.saveTemp.headline,
          "image":this.saveTemp.itemsHeadline,
          "author":this.saveTemp.author,
          "content":this.saveTemp.content,
          "publishDate":this.saveTemp.publishDate,
          "tags":this.saveTemp.tags
        }
      }
       if(this.saveTemp.tags.indexOf(' ') !== -1){
          var tagSplit = this.saveTemp.tags.split(" ");
          data.variables["tags"] = tagSplit
        }

          const postSuccess = axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
                console.log(response)
                return true;

              }).catch((err) => {
                console.log(err)
                return false
              });
            return await postSuccess;

    },



    publishTempMethod(id){
      this.publishTemp = id;
    },
    saveTempMethod(items){
      this.saveTemp = items;
    },
    //vee validate
    getError (fieldName) {
      return this.errors.first(fieldName)
    },
    validate () {
        this.$validator.validateAll().then(isValid => {
          this.$emit('on-submit', this.registerForm, isValid)
        })
     },
     //modal 
     async showSwal (type) {
   if (type === 'warning-message-and-cancel') {

          swal({
            title: 'Are you sure?',
            text: 'This file will be uploaded on the main page.',
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes',
            cancelButtonText: 'No',
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            buttonsStyling: false
          }).then( async () => {
            successerror();
          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              swal({
                title: 'Cancelled',
                text: 'You file is safe',
                type: 'error',
                confirmButtonClass: 'btn btn-info btn-fill',
                buttonsStyling: false
              })
            }
          });

          const successerror = ()=>{
            if (this.saveThis()) {
              swal({
                title: 'success',
                type: 'success',
                confirmButtonClass: 'btn btn-info btn-fill',
                type: 'success',
                buttonsStyling: false
              }).then(() => {
                this.refreshThis();
              });
                
            }else {
              swal({
                title: 'error',
                type: 'warning',
                buttonsStyling: false
              });
            }
          }
          
        }  else if (type === 'input-field') {
          swal({
            title: 'Input something',
            html: '<div class="form-group">' +
            '<input id="input-field" type="text" class="form-control" />' +
            '</div>',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            buttonsStyling: false
          }).then(function (result) {
            swal({
              type: 'success',
              html: 'You entered',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false

            })
          }).catch(swal.noop)
          console.log(swal.noop)
        }
      },
   refreshThis(){
      this.$router.go(this.$router.currentRoute)
    },
},//methods

mounted(){
this.getNews();
}//mounted
};//export default
</script>

<style scoped>
.extended-forms .el-select {
    width: 100%;
    margin-bottom: 30px;
  }
.preview-content{
  overflow: hidden;
}
.preview-content p img{
  width: 100% !important;
}

.editHide{
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  height: 0px !important;
  overflow: hidden;
  border:none;
}
.editShow{
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  height: auto !important;
}
.displayClassNone{
  display: none;
}
.displayClass{
  display:block;
}

.itemsContentClass{
  height:80px;
  overflow:hidden;
  overflow-y: scroll;
}
.smallClass{
  display:none;
}
.cnt-own{
  margin-bottom: 20px;
  padding-top: 10px !important;
  position:relative;
  cursor:pointer;
} 


.fade-enter-active,
.fade-leave-active {
transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
opacity: 0;
}

#d1_ifr{
  background-color:black;
  height: 500px;
}


.form-control.error {
  border-color: #E84444;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(232,68,68,.6);
}

.quilWrapper.error {
  border-color: #E84444;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(232,68,68,.6);
}

.edit-class{
    position: absolute;
    right: 11px;
    top: 9px;
    font-size: 15px;
    opacity: 0.4;
    cursor: pointer
}


</style>


